//可以写一些公共样式
@import './variable';
@import './mixin';

html {
  width: 100%;
  height: 100%;
}
body {
  height: 100%;
  font-family: "PingFang SC","Lantinghei SC","Microsoft YaHei",arial,"\5b8b\4f53",sans-serif,tahoma ;
}


.dialog-container .el-dialog{
  background: #081a48;
  border-radius:10px;
  overflow: hidden;
}
.dialog-container .el-dialog__header{
  padding: 0;
  height: 54px;
  line-height: 54px;
  padding:0 28px;
  border-bottom: 2px solid #003b93;
  background: #082779;
  color: #ffffff;
}
.dialog-container .el-dialog__header .el-icon-close{
  font-size: 26px;
}
.dialog-container .el-dialog__title{
  color: #ffffff;
}
.dialog-container .el-dialog__body{
  padding: 0;
}
.dialog-container .dialog-box{
  padding: 0 28px;
}
.dialog-container .dialog-box h4{
  font-size: 14px;
  color: #ffffff;
  padding: 4px;
  line-height: 42px;
  font-weight: 400;
}

.dialog-container .el-form-item__label{
  line-height: 28px;
  vertical-align: middle;
}
.dialog-container  .el-input{
  width: 325px;
  height: 28px;
  line-height: 28px;
  vertical-align: top;
}
.dialog-container .el-select{
  vertical-align: top;
}
.dialog-container .el-input__inner{
  background: #0b2876;
  border: 1px solid #0352dd;
  color: #ffffff;
  height: 28px;
  line-height: 28px;
}
.el-select-dropdown__item.hover {
  background: #0071ab !important;
}
.dialog-container .el-form-item__label{
  color: #ffffff;
}
.dialog-container .form-box{
  padding-top: 20px;
}
.dialog-container .form-box .input-box{
  width: 470px;
}
.dialog-container .btn-box{
  margin-bottom: 40px;
  text-align: center;
}
.dialog-container .btn-box span{
  display: inline-block;
  text-align: center;
  width: 110px;
  height: 34px;
  line-height: 34px;
  margin: 0 29px;
  background: #07306e;
  color: #c5ecff;
  // border: 1px solid #1855a8;
  border: 1px solid #07306e;
  font-size: 14px;
  text-align: center;
  cursor: pointer;
}
.dialog-container .btn-box span:hover{
  background-image: linear-gradient(-90deg, #2f5efc 0%, #0096ff 100%), linear-gradient(#03298c, #03298c);
  background-blend-mode: normal, normal;
}

.select-file-btn{
  position: absolute;
  right: 0;
  top: 0;
  height: 28px;
  padding: 0 15px!important;
  line-height: 28px!important;
}

.fade-enter-active, .fade-leave-active {
  transition: all .2s ease;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}



