// 解决左右出现滚动条时 左右晃动问题
html {
  overflow-y: scroll;
}
:root {
  overflow-y: auto;
  overflow-x: hidden;
}
:root body {
  position: absolute;
}
body {
  width: 100vw;
  overflow: hidden;
}

// placeholder 样式
input::-webkit-input-placeholder {
  /* WebKit browsers */
  color: #b8d7ff!important
}

input:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #b8d7ff!important
}

input::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #b8d7ff!important
}

input:-ms-input-placeholder {
  /* Internet Explorer 10+ */
  color: #b8d7ff!important
}

/*滚动条样式*/
::-webkit-scrollbar{width:6px;height:10px;}
::-webkit-scrollbar-thumb{ background:rgba(28,83,137,.7); border-radius: 5px;}




