.login_title{
  width: 625px;
  height: 110px;
  background: url("../../assets/img/login_title.png") no-repeat;
}
.login_content{
  width: 625px;
  height: 400px;
  overflow: hidden;
  background: rgba(10,99,219,.7) url("../../assets/img/login_bg.png") no-repeat;
}
.conter-box{
  width: 325px;
  height:auto;
  margin: 100px auto 0;
}
.conter-box div{
  border-bottom: 1px solid #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}
.conter-box div input{
  height: 30px;
  color: #fff;
  font-size: 14px;
  background: none;
  border:none;
  padding-left: 10px;
}
.conter-box div i{
  color: #fff;
  font-size: 26px;
}
.login-user input{
  width: 290px;
}
.login-password{
  margin-top: 30px;
}
.login-password i{
  font-size: 25px;
}
.login-password input{
  width: 258px;
}
.login-register{
  width: 100%;
  height: 40px;
  line-height:40px;
  margin-top:45px;
  font-size: 16px;
  color: #007aec;
  font-weight:bold;
  text-shadow:0px 4px 8px #6daff7;
  text-align:center;
  display:inline-block;
  border:1px solid #fff;
  border-radius:5px;
  background: rgba(225,225,225,.9);
  transition: all .8s;
}
.login-register:hover{
  background: rgba(225,225,225,1);
  color: #007aec;
  transition: all .2s ease-in;
}
.login-warn{
 color: #fff000;
  font-size: 14px;
  text-align: center;
  margin-top:40px;
}
.login-warn i{
  margin-right: 5px;
}

.i-ciphertext{
  cursor:pointer;
}




