body, html {
  font: 12px/1.5 \5FAE\8F6F\96C5\9ED1, \5B8B\4F53, "Hiragino Sans GB", STHeiti, "WenQuanYi Micro Hei", "Droid Sans Fallback", SimSun, sans-serif;
  width: 100%;
  height: 100%;
  line-height: 1.5;
  font-size: 14px;
  color: #333; }

html {
  overflow-y: scroll; }

:root {
  overflow-y: auto;
  overflow-x: hidden; }

:root body {
  position: absolute; }

body {
  width: 100vw;
  overflow: hidden; }

input::-webkit-input-placeholder {
  /* WebKit browsers */
  color: #b8d7ff !important; }

input:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #b8d7ff !important; }

input::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #b8d7ff !important; }

input:-ms-input-placeholder {
  /* Internet Explorer 10+ */
  color: #b8d7ff !important; }

/*滚动条样式*/
::-webkit-scrollbar {
  width: 6px;
  height: 10px; }

::-webkit-scrollbar-thumb {
  background: rgba(28, 83, 137, 0.7);
  border-radius: 5px; }

