.el-checkbox-group .el-checkbox {
  color: #c2cae2;
  transition: all 0.2s ease-in; }
  .el-checkbox-group .el-checkbox .el-checkbox__label:hover {
    color: #fff; }

.el-checkbox-group .is-checked .is-checked + .el-checkbox__label {
  color: #fff; }

.el-checkbox__input .el-checkbox__inner {
  background: #0b1e6b;
  border: solid 1px #2c4fd2 !important; }

.el-checkbox__input.is-checked .el-checkbox__inner {
  background: #0c83fe !important; }

.dropdown .el-select {
  border: 1px solid #0447c2;
  border-radius: 5%; }

.dropdown .el-input__inner {
  border: none;
  background-color: transparent;
