@import './_mixin';

// 这里可重置element-ui样式
.el-checkbox-group {
  .el-checkbox {
    color: #c2cae2;
    @include animation();
    .el-checkbox__label:hover {
      color: #fff;
    }
  }
  .is-checked {
    .is-checked + .el-checkbox__label {
      color: #fff;
    }
  }
}

.el-checkbox__input {
  .el-checkbox__inner {
    background: #0b1e6b;
    border: solid 1px rgba(44, 79, 210, 1) !important;
  }
  &.is-checked {
    .el-checkbox__inner {
      background: #0c83fe !important;
    }
  }
}

//虚拟人物画像-下拉框样式
.dropdown .el-select {
  border: 1px solid #0447c2;
  border-radius: 5%;
}

.dropdown .el-input__inner {
  //border: 1px solid #0447c2;
  border: none;
  background-color: transparent;
  width: 125px;
  height: 34px;
  color: white !important;
}

.el-select-dropdown__list {
  padding: 3px 0 !important;
}

.el-popper .popper__arrow, .el-popper .popper__arrow::after {
  display: none !important;
  border-bottom-color: #0073a8 !important;
}

.el-progress__text {
  color: #3cfaff !important;
  font-size: 20px !important;
}

.el-select-dropdown.el-popper {
  background-color: #0a2167;
  border: 1px solid #0073a8 !important;
  transform: translateY(-11px);
}

.el-select-dropdown__item {
  color: white !important;
  background-color: rgba(5, 42, 84, 0.8) !important;
}
.el-scrollbar{

}
.el-scrollbar__wrap {
  //border: solid 1px #0073a8 !important;
}

.el-select-dropdown__item.hover {
  background: #0071ab !important;
  span {
    color: #ffe70f !important;
  }
}

.el-pagination.is-background {
  width: 100%;
}

//虚拟人物画像-下拉框样式
.virtual-portrait {
  .el-progress-bar__outer {
    cursor: default;
    height: 14px !important;
    background-color: #04193d !important;
    border: solid 1px #1083fe !important;
    border-radius: 0 !important;
    .el-progress-bar__inner {
      border-radius: 0;
      background-image: linear-gradient(90deg, #0078ff 0%, #11abff 50%, #19c6ff 77%, #00fcff 98%, #00fcff 100%), linear-gradient(#000000, #000000);
    }
  }
  .el-progress__text {
    font-size: 22px !important;
    margin-left: 7px;
  }
}




